    export function updateCache<T>(key1: string, key2: string, key3: string, key4: string, value: T): void;
    export function updateCache<T>(arg1: T, arg2: T, arg3?: T, arg4?: T, arg5?: T) {
        if (arg5!= undefined) {
            player_data[String(arg1)][String(arg2)][String(arg3)][String(arg4)] = arg5;
        }
        if (arg4 != undefined&& arg5 == undefined) {
            player_data[String(arg1)][String(arg2)][String(arg3)] = arg4;
        }
        if (arg3!= undefined && arg4 == undefined) {
            player_data[String(arg1)][String(arg2)] = arg3;
        }
        if (arg2 != undefined&& arg3 == undefined) {
            player_data[String(arg1)] = arg2;
        }
        setStorage();
    }

    
    export function _setItem(key: string, value: string) {
        // cc.log("---- key, value------", key, value)
        _dataPool[key] = value;
        _save();
    }

    export function _getItem(key: string, defaultValue: string) {
        if (_dataPool[key]) {
            return _dataPool[key];
        }
        return defaultValue;
    }

    export function _save() {
        cc.sys.localStorage.setItem(ST_GameData, JSON.stringify(_dataPool));
    }

    /**ShakeOn */
    export function getShakeOn() {
        return _getItem(Constant.ST_ShakeOn, "1") == "1";
    }
    /**ShakeOn */
    export function setShakeOn(value: boolean) {
        _setItem(Constant.ST_ShakeOn, value ? "1" : "0");
    }

    export function getPrivacyOn() {
        return _getItem(Constant.ST_Privacy, "1") == "1";
    }
    export function setPrivacyOn(value: boolean) {
        _setItem(Constant.ST_Privacy, value ? "1" : "0");
    }

    export function getSignOnceOn() {

        let ret = _getItem(Constant.ST_SignOnceOnTime, "0");
        if (!ret) {
            ret = "0";
        }
        if (new Date().toDateString() != ret){//
            return false
        }
        return _getItem(Constant.ST_SignOnceOn, "1") == "1";
    }
    export function setSignOnceOn(value: boolean) {
        _setItem(Constant.ST_SignOnceOnTime,new Date().toDateString())
        _setItem(Constant.ST_SignOnceOn, value ? "1" : "0");
    }

