import { param } from "./type";

var TAG = '_wechat.ts';
class _wechat {
    private static Instance: _wechat = null;
    private videoAd: any = null;
    private bannerAd: any = null;
    private videoId: string = "";
    private bannerId: string = "";
    private insertId: string = "";
    private sdkInitEnd: boolean = false;
    private onVideoAdCallBack: Function = null;
    private insertAdTime = 0;
    constructor() {
        console.log(TAG, 'init platform channel: _wechat');
    }
    public static getInstance(): _wechat {
        if (this.Instance == null) {
            this.Instance = new _wechat();
        }
        return this.Instance;
    }
    initAdParams(param: param) {
        if (this.sdkInitEnd) {
            return;
        }
        this.sdkInitEnd = true;
        this.insertId = param.insertId;
        this.bannerId = param.bannerId;
        this.videoId = param.videoId;
    }
    initBannerAd() {
        // @ts-ignore
        const { windowWidth, windowHeight } = wx.getSystemInfoSync();
        var targetBannerAdWidth = 120;
        // 
        // @ts-ignore
        this.bannerAd = wx.createBannerAd({
            adUnitId: this.bannerId,
            adIntervals: 120,
            style: {
                width: targetBannerAdWidth,
                top: windowHeight - (targetBannerAdWidth / 16 * 9), // 
            }
        });
        this.bannerAd.onError((err: any) => {
            switch (err['errCode']) {
                case 1000:
                    console.warn(TAG, '');
                    break;
                case 1001:
                    console.warn(TAG, '');
                    break;
                case 1002:
                    console.warn(TAG, '');
                    break;
                case 1003:
                    console.warn(TAG, '');
                    break;
                case 1004:
                    console.warn(TAG, ', ');
                    break;
                case 1005:
                    console.warn(TAG, '');
                    break;
                case 1007:
                    console.warn(TAG, '');
                    break;
                case 1008:
                    console.warn(TAG, '');
                    break;
                default:
                    break;
            }
        });
        // 
        this.bannerAd.style.left = (windowWidth - targetBannerAdWidth) / 2;
        // 
        // 
        this.bannerAd.onResize((size: any) => {
            console.log(TAG, size.width, size.height);
            if (size.width == 0 || size.height == 0) {
                return;
            }
            //  banner 
            if (targetBannerAdWidth != size.width) {
                targetBannerAdWidth = size.width;
                this.bannerAd.style.top = windowHeight - (size.height);
                this.bannerAd.style.left = (windowWidth - size.width) / 2;
            }
        });
        //load
        this.bannerAd.onLoad(function () {
            console.log(TAG, "banner!");
        });
    }
    bannerAd_show() {
        if (this.bannerAd) {
            this.bannerAd.show();
        }
    }
    bannerAd_hide() {
        if (this.bannerAd) {
            this.bannerAd.hide();
            this.initBannerAd();
        }
    }
    initVideoAd() {
        //@ts-ignore
        if (wx.createRewardedVideoAd) {
            return;
        }
        //@ts-ignore
        this.videoAd = wx.createRewardedVideoAd({
            adUnitId: this.videoId,
            multiton: false
        });
        this.videoAd.onClose((res: any) => {
            if (res && res.isEnded) {
                this.onVideoAdCallBack(true);
            } else {
                this.onVideoAdCallBack(false);
            }
        });
        this.videoAd.onError((err: any) => {
            switch (err.errCode) {
                case 1000:
                    console.warn(TAG, ``);
                    break;
                case 1001:
                    console.warn(TAG, ``);
                    break;
                case 1002:
                    console.warn(TAG, ``);
                    break;
                case 1003:
                    console.warn(TAG, ``);
                    break;
                case 1004:
                    console.warn(TAG, ``);
                    break;
                case 1005:
                    console.warn(TAG, ``);
                    break;
                case 1006:
                    console.warn(TAG, ``);
                    break;
                case 1007:
                    console.warn(TAG, ``);
                    break;
                case 1008:
                    console.warn(TAG, ``);
                    break;
            }
        });
        this.videoAd.onLoad((res: any) => {
            this.videoAd.show();
        });
        this.videoAd.load();
    }
    videoAd_show(onVideoCall: Function) {
        this.onVideoAdCallBack = onVideoCall;
        this.videoAd != null ? this.videoAd.load() : this.initVideoAd();
    }
    insterAd_show() {
        let time = new Date().getTime();
        if (time - this.insertAdTime < 30000) {
            console.log(TAG, '...');
            return;
        }
        this.insertAdTime = time;
        //@ts-ignore
        let interstitialAd = tt.createInterstitialAd({
            adUnitId: this.insertId
        });
        interstitialAd.onError((err: any) => {
            switch (err.errCode) {
                case 1000:
                    console.warn(TAG, ``);
                    break;
                case 1001:
                    console.warn(TAG, ``);
                    break;
                case 1002:
                    console.warn(TAG, ``);
                    break;
                case 1003:
                    console.warn(TAG, ``);
                    break;
                case 1004:
                    console.warn(TAG, ``);
                    break;
                case 1005:
                    console.warn(TAG, ``);
                    break;
                case 1006:
                    console.warn(TAG, ``);
                    break;
                case 1007:
                    console.warn(TAG, ``);
                    break;
                case 1008:
                    console.warn(TAG, ``);
                    break;
            }
        });
        interstitialAd.onClose(() => {
            interstitialAd.destroy();
        });
        interstitialAd.load().then(() => {
            interstitialAd.show();
        });
    }
}
export const ch_wechat = _wechat;