import { Export } from "../../syyx_sdk/export_sdk";
import { param } from "./type";
const TAG = '_oppo.ts';
class _oppo {
    private static Instance: _oppo;
    private bannerId: string = "";
    private videoId: string = "";
    private bannerAd: any = null;
    private videoAd: any = null;
    private OnVideoCallBackListener: Function = null;
    private bannerAd_showNum: number = 20;
    private sdkInitEnd: boolean = false;
    private initTime: number = 0;
    constructor() {
        console.log(TAG, 'init platform channel: oppo_play');
    }
    public static getInstance(): _oppo {
        if (this.Instance == null) {
            this.Instance = new _oppo();
            this.Instance.initTime = new Date().getTime();
        }
        return this.Instance;
    }
    initAdParams(param: param) {
        if (this.sdkInitEnd) {
            return;
        }
        this.sdkInitEnd = true;
        this.bannerId = param.bannerId;
        this.videoId = param.videoId;
        this.initVideoAd();
        this.initBannerAd();
        ///> '1051'minPlatformVersion> ='1051'qg.initAdService(object);
    }
    initBannerAd() {
        let self: _oppo = this;
        //@ts-ignore
        var bannerAd = qg.createBannerAd({
            posId: this.bannerId
        });
        this.bannerAd = bannerAd;
        this.bannerAd.onHide(() => {
            self.bannerAd_showNum -= 1;
            console.log(TAG, "banner !");
        });
        this.bannerAd.onShow(() => {
            console.log(TAG, "banner !");
        });
        this.bannerAd.onError((err: any) => {
            switch (err['errCode']) {
                case 1001:
                    console.warn(TAG, '1.!');
                    console.warn(TAG, '2.!');
                    console.warn(TAG, '3.!');
                    console.warn(TAG, '4.!');
                    break;
                case 1002:
                    console.warn(TAG, '19/14Android!');
                    break;
                case 1003:
                    console.warn(TAG, '!');
                    break;
                case 1004:
                    console.warn(TAG, '1.OPPO!');
                    console.warn(TAG, '1.!');
                    console.warn(TAG, '1.20003/OPPO!');
                    break;
            }
        });
    }
    bannerAd_show() {
        if (this.bannerAd_showNum <= 0 || new Date().getTime() - this.initTime < 60000) {
            return;
        }
        this.bannerAd.show();
    }
    bannerAd_hide() {
        if (this.bannerAd) {
            this.bannerAd.hide();
            this.bannerAd.destroy();
            this.initBannerAd();
        }
    }
    initVideoAd() {
        let self: _oppo = this;
        //@ts-ignore
        this.videoAd = qg.createRewardedVideoAd({
            posId: self.videoId
        });
        this.videoAd.onLoad(function () {
            self.videoAd.show();
        });
        this.videoAd.onError(function (err: any) {
            switch (err['errCode']) {
                case 1001:
                    console.warn(TAG, '1.!');
                    console.warn(TAG, '2.!');
                    console.warn(TAG, '3.!');
                    console.warn(TAG, '4.!');
                    break;
                case 1002:
                    console.warn(TAG, '19/14Android!');
                    break;
                case 1003:
                    console.warn(TAG, '!');
                    break;
                case 1004:
                    console.warn(TAG, '1.OPPO!');
                    console.warn(TAG, '1.!');
                    console.warn(TAG, '1.20003/OPPO!');
                    break;
            }
            //@ts-ignore
            qg.showToast({
                title: '',
                icon: 'none',
                duration: 2000
            });
        });
        this.videoAd.onClose((res: any) => {
            if (res.isEnded) {
                self.OnVideoCallBackListener(true);
            } else {
                self.OnVideoCallBackListener(false);
            }
        });
    }
    videoAd_show(OnVideoCall: Function) {
        // this.OnVideoCallBackListener = OnVideoCall;
        // this.videoAd.load();

        Export.show_video(() => { 
            cc.log('');
            OnVideoCall && OnVideoCall(true);
        },()=>{
            OnVideoCall && OnVideoCall(false);
        })
        
    }
    _oppoPush(packgeName: string = '') {
        // @ts-ignore
        qg.navigateToMiniGame({ pkgName: packgeName });
    }
    shakeDevice() {
        // @ts-ignore
        qg.vibrateShort({});
    }
}
export const ch_oppo = _oppo;