import { param } from "./type";
const TAG = '_bytedance.ts';
class _bytedance {
    private static Instance: _bytedance;
    private recordTimeStamp: number = 0;
    private videoPath: string = "";
    private recordTimeLength: number = 0;
    private appIdParam: string = "";
    private bannerAdParam: string = "";
    private vedioAdParam: string = "";
    private insertAdParam: string = "";
    private shareRecordDesc: string[] = [];
    private bannerAd: any = null;
    private videoAd: any = null;
    private bannerBox: any = null;
    private OnVideoCallBackListener: Function = null;
    private sdkInitEnd: boolean = false;
    private insertAdTime: number = 0;
    private recordManager: any = null;
    private show_start: boolean = false;
    private show_end: boolean = false;
    private userQuery: string = '';
    private constructor() {
        console.log(TAG, 'init platform channel: bytedance');
    }
    public static getInstance(): _bytedance {
        if (this.Instance == null) {
            this.Instance = new _bytedance();
        }
        return this.Instance;
    }
    initRecordListener() {
        let that: _bytedance = this;
        //@ts-ignore
        this.recordManager = tt.getGameRecorderManager();
        this.recordManager.onStart(() => {
            that.videoPath = "";
            that.recordTimeStamp = Math.floor(new Date().getTime());
            that.showToast(that.show_start, '', 'success', 800);
        });
        this.recordManager.onError((err: any) => {
            console.warn(TAG, ':', err['errMsg']);
        });
        this.recordManager.onStop((res: any) => {
            that.recordTimeLength = Math.floor(new Date().getTime()) - that.recordTimeStamp;
            if (that.recordTimeLength >= 3000) {
                that.videoPath = res['videoPath'];
                that.showToast(that.show_end, '', 'success', 1000);
            }
        });
    }
    luzhiVideo_start(startTip: boolean = false, maxSecond: number = 300, isMarkOpen: boolean = false) {
        this.show_start = startTip;
        this.recordManager.start({ duration: maxSecond, isMarkOpen: isMarkOpen });
    }
    luzhiVideo_stop(endTip: boolean = true) {
        this.show_end = endTip;
        this.recordManager.stop();
    }
    luzhiVideo_share(onShareCall: Function, query: string = 'k1=v1&k2=v2') {
        if (onShareCall == null || this.videoPath == null || this.recordTimeLength < 3000) {
            if (onShareCall == null) {
                console.warn(TAG, 'null!');
            }
            else if (this.videoPath == null) {
                console.warn(TAG, '!');
            }
            else {
                console.warn(TAG, '3!');
            }
            return;
        }
        this.userQuery = query;
        this.video_share(onShareCall, this.userQuery);
    }
    video_share(OnShareCall: Function, query: string = 'k1=v1&k2=v2') {
        let that: _bytedance = this;
        // @ts-ignore
        tt.shareAppMessage({
            channel: "video",
            query: query,
            extra: {
                videoPath: that.videoPath,
                video_title: that.shareRecordDesc[1],     // 
                videoTopics: [that.shareRecordDesc[0]],   // ()
                hashtag_list: [that.shareRecordDesc[0]],  // ()
                withVideoId: false,  //    true  success  videoId
                videoTag: ''         // 
            },
            success(res: any) {
                OnShareCall(true);
                that.videoPath = "";
                that.recordTimeLength = 0;
                that.showToast(true, '', 'success', 1000);
            },
            fail(e: any) {
                console.warn(TAG, `:${JSON.stringify(e)}`);
                OnShareCall(false);
                that.showToast(true, '', 'fail', 1000);
            }
        });
    }
    getVideoInfo(id: string) {
        //@ts-ignore
        tt.request({
            url: "https://gate.ssdk.com/developer/api/get_video_info",
            method: "POST",
            data: {
                alias_ids: [id],
            },
            success: (res: any) => {
                if (res.data.data[0].video_info.cover_url) {
                    console.log(res.data.data[0].video_info); //  cover_url
                } else {
                    //5
                    setTimeout(() => {
                        this.getVideoInfo(id);
                    }, 5000);
                }
            },
        });
    }
    /**
     * 
     * @param show 
     * @param message 
     * @param icon (success,loading,none,fail),,,
     * @param duration 
     */
    showToast(show: boolean = false, message: string, icon: string = 'none', duration: number = 1000) {
        if (!show) {
            return;
        }
        //@ts-ignore
        tt.showToast({
            title: message,
            icon: icon,
            duration: duration,
        });
    }
    buttonShare(OnShareCall: Function) {
        // @ts-ignore
        tt.shareAppMessage({
            success() {
                OnShareCall(true);
                console.log(TAG, '!');
            },
            fail(e: Object) {
                OnShareCall(false);
                console.warn(TAG, `:${JSON.stringify(e)}`);
            }
        });
    }
    shakeDevice() {
        // @ts-ignore
        tt.vibrateShort({
            fail(res: Object) {
                console.warn(TAG, `:${res}`);
            }
        });
    }
    shakeDeviceLong() {
        // @ts-ignore
        tt.vibrateLong({
            fail(res: Object) {
                console.warn(TAG, `:${res}`);
            }
        });
    }
    showTTGameIcon(): boolean {
        //@ts-ignore
        const systemInfo = tt.getSystemInfoSync();
        if (systemInfo.platform === "ios") {
            return false;
        }
        let appName: string[] = ['Douyin', 'Toutiao', 'news_article_lite'];
        return appName.indexOf(systemInfo.appName) === -1 ? false : true;
    }
    openMoreGame() {
        //@ts-ignore
        tt.onMoreGamesModalClose(function () {
            console.warn(TAG, "modal closed!");
        });
        //@ts-ignore
        tt.onNavigateToMiniProgram(function (res) {
            console.log(res.errCode);
            console.log(res.errMsg);
        });
        //@ts-ignore
        const systemInfo = tt.getSystemInfoSync();
        // iOS 
        if (systemInfo.platform !== "ios") {
            //@ts-ignore
            tt.showMoreGamesModal({
                appLaunchOptions: [
                    {
                        appId: this.appIdParam,
                        query: "foo=bar&baz=qux",
                        extraData: {}
                    }
                    // {...}
                ],
                success() {
                    console.log(TAG, `success`);
                },
                fail(res: any) {
                    console.log(TAG, `${JSON.stringify(res)}`);
                }
            });
        }
    }
    initAdParams(param: param) {
        if (this.sdkInitEnd) {
            return;
        }
        this.sdkInitEnd = true;
        this.initRecordListener();
        this.appIdParam = param.appId;
        this.insertAdParam = param.insertId;
        this.bannerAdParam = param.bannerId;
        this.vedioAdParam = param.videoId;
        this.shareRecordDesc = param.shareDesc;
        try {
            this.initBannerAd();
        } catch (error) {
            console.warn(TAG, `Init ad params:${error}`);
        }
    }
    initGameBanner() {
        //@ts-ignore
        const { windowWidth, windowHeight } = tt.getSystemInfoSync();
        var bannerBoxWidth = windowWidth;
        //@ts-ignore
        this.bannerBox = tt.createMoreGamesBanner({
            style: {
                width: bannerBoxWidth,
                verticalAlign: 'bottom',
                horizontalAlign: 'center'
            },
            appLaunchOptions: [
                {
                    appId: this.appIdParam,
                    query: "foo=bar&baz=qux",
                    extraData: {}
                }
                // {...}
            ],
        })
        this.bannerBox.show();
        this.bannerBox.onTap(() => {
            console.log(TAG, ``);
        });
    }
    gameBanner_show() {
        if (this.bannerBox) {
            this.bannerBox.show();
        } else {
            this.initGameBanner();
        }
    }
    gameBanner_hide() {
        if (this.bannerBox) {
            this.bannerBox.hide();
        }
    }
    initBannerAd() {
        // @ts-ignore
        const { windowWidth, windowHeight } = tt.getSystemInfoSync();
        var targetBannerAdWidth = 120;
        // 
        // @ts-ignore
        this.bannerAd = tt.createBannerAd({
            adUnitId: this.bannerAdParam,
            style: {
                width: targetBannerAdWidth,
                top: windowHeight - (targetBannerAdWidth / 16 * 9), // 
            }
        });
        this.bannerAd.onError((err: any) => {
            switch (err['errCode']) {
                case 1000:
                    console.warn(TAG, '');
                    break;
                case 1001:
                    console.warn(TAG, '');
                    break;
                case 1002:
                    console.warn(TAG, '');
                    break;
                case 1003:
                    console.warn(TAG, '');
                    break;
                case 1004:
                    console.warn(TAG, ', ');
                    break;
                case 1005:
                    console.warn(TAG, '');
                    break;
                case 1007:
                    console.warn(TAG, '');
                    break;
                case 1008:
                    console.warn(TAG, '');
                    break;
                case 120002:
                    console.warn(TAG, '');
                    break;
                default:
                    break;
            }
        });
        // 
        this.bannerAd.style.left = (windowWidth - targetBannerAdWidth) / 2;
        // 
        // 
        this.bannerAd.onResize((size: any) => {
            console.log(TAG, size.width, size.height);
            if (size.width == 0 || size.height == 0) {
                return;
            }
            //  banner 
            if (targetBannerAdWidth != size.width) {
                targetBannerAdWidth = size.width;
                this.bannerAd.style.top = windowHeight - (size.height);
                this.bannerAd.style.left = (windowWidth - size.width) / 2;
            }
        });
        //load
        this.bannerAd.onLoad(function () {
            console.log(TAG, "banner!");
        });
    }
    bannerAd_show() {
        if (this.bannerAd) {
            this.bannerAd.show();
        }
    }
    bannerAd_hide() {
        if (this.bannerAd) {
            this.bannerAd.hide();
            this.initBannerAd();
        }
    }
    showLoading() {
        //@ts-ignore
        tt.showLoading({ title: "..." });
    }
    hideLoading() {
        //@ts-ignore
        tt.hideLoading({});
    }
    initVideo() {
        // @ts-ignore
        this.videoAd = tt.createRewardedVideoAd({
            adUnitId: this.vedioAdParam,
            multiton: false,
            multitonRewardedMsg: ''
        });
        this.videoAd.onError((err: any) => {
            switch (err['errCode']) {
                case 1000:
                    console.warn(TAG, '');
                    break;
                case 1001:
                    console.warn(TAG, '');
                    break;
                case 1002:
                    console.warn(TAG, '');
                    break;
                case 1003:
                    console.warn(TAG, '');
                    break;
                case 1004:
                    console.warn(TAG, ', ');
                    break;
                case 1005:
                    console.warn(TAG, '');
                    break;
                case 1007:
                    console.warn(TAG, '');
                    break;
                case 1008:
                    console.warn(TAG, '');
                    break;
                case 120002:
                    console.warn(TAG, '');
                    break;
                default:
                    console.warn(TAG, `:https://microapp._bytedance.com/docs/zh-CN/mini-game/develop/open-capacity/ads/rewarded-video-ad/rewarded-video-ad-on-error#%E9%94%99%E8%AF%AF%E7%A0%81%E8%AF%A6%E6%83%85`);
                    break;
            }
        });
        this.videoAd.onClose((res: any) => {
            if (res['isEnded']) {
                this.OnVideoCallBackListener(true);
            } else {
                this.OnVideoCallBackListener(false);
            }
        });
        this.videoAd.onLoad((res: any) => {
            this.videoAd.show();
            this.hideLoading();
        });
        this.videoAd.load();
        this.showLoading();
    }
    videoAd_show(OnVideoCall: Function) {
        this.OnVideoCallBackListener = OnVideoCall;
        if (this.videoAd != null) {
            this.showLoading();
            this.videoAd.load();
        } else {
            this.initVideo();
        }
    }
    insterAd_show() {
        let time = new Date().getTime();
        if (time - this.insertAdTime < 35000) {
            console.log(TAG, '...');
            return;
        }
        this.insertAdTime = time;
        //@ts-ignore
        let interstitialAd = tt.createInterstitialAd({
            adUnitId: this.insertAdParam
        });
        interstitialAd.onError((err: any) => {
            switch (err['errCode']) {
                case 2001:
                    console.warn(TAG, '');
                    break;
                case 2002:
                    console.warn(TAG, '');
                    break;
                case 2003:
                    console.warn(TAG, '');
                    break;
                case 2004:
                    console.warn(TAG, '');
                    break;
                case 2005:
                    console.warn(TAG, '');
                    break;
                default:
                    //   
                    break;
            }
        });
        interstitialAd.onClose(() => {
            interstitialAd.destroy();
        });
        interstitialAd.load().then(() => {
            interstitialAd.show();
        });
    }
    reportAnalytics(values: string | null, customObject: Object = {}) {
        //@ts-ignore
        tt.reportAnalytics(values, customObject);
    }
    showFavoriteGuide() {
        //    type = 'bar' 
        //   
        //   - 10s 
        //   - 2 
        //@ts-ignore
        tt.showFavoriteGuide({
            type: "bar",
            content: "",
            position: "bottom",
            success(res: any) {
                console.log("", res['errMsg']);
            },
            fail(err: any) {
                console.log("", err['errMsg']);
            },
        });
    }
    openAwemeUserProfile(reslove: Function) {
        // @ts-ignore
        tt.openAwemeUserProfile({
            success(res: Object) {
                reslove(true);
            },
            fail(e: Object) {
                reslove(false);
            }
        });
    }
    login() {
        //@ts-ignore
        tt.login({
            force: true,
            success(res: any) {
                console.log(TAG, '');
                _bytedance.Instance.getUserInfo();
            },
            fail(err: any) {
                console.warn(TAG, '');
            }
        });
    }
    getUserInfo() {
        //@ts-ignore
        tt.getUserInfo({
            withCredentials: false,
            success(res: any) {
                console.log(TAG, ':', res.userInfo);
            },
            fail(e: any) {
                console.warn(TAG, ':', e);
            }
        });
    }
}
export const ch_bytedance = _bytedance;