export class SkinInfo {
    public Id: number;
    /**0 1 2 */
    public State: number;
    public Price: number;

    constructor(str: string) {
        // cc.log(" SkinInfo :", str);
        let info: any = JSON.parse(str);
        if (info) {
            if (info.Id) {
                this.Id = parseInt(info.Id);
            } else {
                cc.log("Id !");
            }

            if (info.State) {
                this.State = parseInt(info.State);
            } else {
                cc.log("State !");
            }

            if (info.Price) {
                this.Price = parseInt(info.Price);
            } else {
                cc.log("Price !");
            }
        } else {
            cc.log(" SkinInfo !");
        }

    }

    public ToString() {
        return `{"Id":"${this.Id}","State:"${this.State}","Price":"${this.Price}"}`;
    }
}

export class Levelitem {
    public Id: number;
    public State: number;//0  1   2  3
    public Trail: number;//
    public TrailState: number;// 01
    public prog: number;//,

    constructor(str: string) {
        let info: any = JSON.parse(str);
        if (info) {
            if (info.Id) {
                this.Id = parseInt(info.Id);
            } else {
                cc.log("Id !");
            }

            if (info.State) {
                this.State = parseInt(info.State);
            } else {
                cc.log("State !");
            }

            if (info.Trail) {
                this.Trail = parseInt(info.Trail);
            } else {
                cc.log("Trail !");
            }

            if (info.TrailState) {
                this.TrailState = parseInt(info.TrailState);
            } else {
                cc.log("TrailState !");
            }

            if (info.prog) {
                this.prog = parseInt(info.prog);
            } else {
                cc.log("TrailState !");
            }
        } else {
            cc.log(" Levelitem !");
        }
    }
    public ToString() {
        return `{"Id":"${this.Id}","Trail":"${this.Trail}","State:"${this.State}","TrailState":"${this.TrailState}"}`;
    }
}
