    static get_adid_gc_statuc(_type: string) {

        if (this.adIdMap.has(_type)) {
            let array = this.adIdMap.get(_type);
            let res = JSON.parse(array[0]);
            if (Boolean(res)) {
                return res.gc_status;
            }
            else return null;
        }
        else return null;
    }

    static guobao_get_channel_ad_id(_type: string): string {
        if (this.adIdMap.has(_type)) {
            let array = this.adIdMap.get(_type);
            let res = JSON.parse(array[0]);
       
            if (Boolean(res)) {
                return res.adv_id;
            }
            else return null;
        }
        else return null;
    }


